@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
body {
  margin: 0;
  /* font-family:'Share Tech Mono', monospace; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://github.com/Michael-Veiga/Portfiolio/blob/master/images/crocodile.jpg?raw=true);
  background-size: cover !important;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.g-card {
  margin: 20px;
}

.g-card-image {
  border-radius: 10px;
  width: 575px;
  height: 325px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.g-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.g-card-title {
  font-size: 24px;
  margin: 0px;
}

.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.p-pic-image {
  height: 300px;
  width: 300px;
}

.success-msg {
  color: #57A773;
  padding: 10px 15px;
}

.error-msg {
  color: #EE6352;
  padding: 10px 15px;
}

.fab {
  color: white
}

.far {
  color: white
}

.grow {
  transition: all 0.2s ease-in-out;
  }

.grow:hover {
  transition: all 0.2s ease-in-out;
  transform: scale(1.1);
  -webkit-filter: drop-shadow(5px 5px 2px #65f135);
          filter: drop-shadow(5px 5px 2px #65f135);
}

.nav-effect {
transition: border-width 0.6s linear;
box-shadow: 0 0 13px 2px #65f135;
}

.foot-effect {
  transition: border-width 0.6s linear;
  box-shadow: 0 -3px 13px 2px #65f135;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 3rem;
}

.item-grow {
  transition: all 0.2s ease-in-out;
}

.item-grow:hover {
  transform: scale(1.1);
  text-shadow: 5px 5px 15px #65f135;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

